.footer {
  padding: 68px 0;

  @include viewport--md {
    padding: 35px 0;
  }
}

.footer .container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  @include viewport--md {
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: flex-start;
  }
}

.footer__socials {
  @include list-style-reset;
  display: flex;
  flex-wrap: wrap;
  width: 31.5%;

  @include viewport--md {
    margin: 0 0 35px;
    width: auto;
  }
}

.footer__socials li:not(:first-of-type) {
  margin: 0 0 0 17px;
}

.footer__socials a {
  @include center-xy-by-flex-row;
  min-height: 40px;
  padding: 0 10px;

  &[title="Chat"] {
    padding: 0 18px 0 15px;

    & p {
      margin: 0 0 0 10px;
      letter-spacing: -0.1px;
    }
  }
}

.footer__socials p {
  font-family: $font-family--main;
  font-size: 14px;
  line-height: 1.2;
}

.footer__contacts {
  @include list-style-reset;
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: space-between;
  width: 61.5%;

  @include viewport--md {
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: flex-start;
    width: auto;
  }
}

.footer__contacts li {
  @include viewport--md {
    margin: 0 0 20px;
  }
}

.footer__contact-headline {
  margin: 0;

  font-size: 16px;
  line-height: 22px;

  @include viewport--md {
    margin: 0 0 3px;

    font-size: 14px;
    line-height: 16px;
    letter-spacing: -0.2px;
  }
}

.footer__contact {
  display: inline;

  font-size: 16px;
  line-height: 19px;
  font-weight: 700;
  font-style: normal;
  color: $color-default-black;

  &:not(address) {
    @include on-hover;
  }

  &[href^="mailto"] {
    color: $color-violet;
  }

  @include viewport--md {
    font-size: 14px;
    line-height: 17px;
    letter-spacing: 0;
  }
}

.footer__copyright {
  min-width: 100%;
  margin: 71px 0 0;

  text-align: center;
  font-size: 12px;
  line-height: 15px;
  color: $color-grey;

  @include viewport--md {
    margin: 15px 0 0;

    text-align: left;
  }
}

.footer__copyright p {
  margin: 0;
}

.footer__copyright span {
  &:last-of-type:not(:only-of-type) {
    margin: 0 0 0 11px;
  }

  @include viewport--md {
    &:last-of-type:not(:only-of-type) {
      margin: 0;
    }
  }
}

.footer__copyright-address {
  display: inline;

  font-style: normal;
}

.footer__copyright-address span,
.footer__copyright-requisites span {
  display: inline;

  @include viewport--md {
    display: block;
  }
}
