.delivery {
  @include list-style-reset;
  display: flex;
  justify-content: center;
  padding: 33px 0 57px;
  background-color: $color-white-dark;

  @include viewport--md {
    padding: 50px 0 5px 10px;
  }
}

.delivery__item {
  display: inline-flex;

  svg {
    margin-right: 20px;
  }

  p {
    margin: 0;
    width: 144px;
    font-size: 21px;
    line-height: 25px;

    @include viewport--md {
      width: 102px;
      font-size: 14px;
      line-height: 17px;
    }
  }

  &:nth-child(2) {
    margin-left: 60px;
    svg {
      margin-right: 18px;
    }

    @include viewport--md {
      margin-left: 34px;
    }
  }

  @include viewport--md {
    display: block;

    svg {
      width: 32px;
      height: 32px;
    }
  }
}

