#advantages {
  h2 {
    display: none;
  }
}

.advantages {
  @include list-style-reset;
  position: relative;
  justify-content: space-between;
  display: inline-flex;
  flex-wrap: wrap;
  margin: 0 0 48px;

  @include viewport--sm {
    margin: 0 -24px;
  }
}

.advantages__item {
  position: relative;
  z-index: 1;
  width: 31.3%;
  margin-bottom: 32px;

  &--main {
    width: 66%;

    @include viewport--sm {
      width: 100%;
    }
  }

  &:nth-child(1) {
    margin-bottom: 62px;

    @include viewport--sm {
      margin-bottom: 58px;
    }
  }

  &:nth-child(2)::after {
    content: "";
    z-index: 0;
    position: absolute;
    top: -82px;
    right: -204px;
    width: 240px;
    height: 375px;
    background-image: url('../img/bg/bg-advantages-circles1.png');
    background-repeat: no-repeat;

    @include viewport--sm {
      width: calc(100% + 54px);
      left: -54px;
      top: -25px;
      background-image: url('../img/svg/bg-advantages-circles-mobile2.svg');
    }
  }

  &:nth-child(5) {
    z-index: 0;
    &::after {
      content: "";
      z-index: 0;
      position: absolute;
      top: -350px;
      right: -133px;
      width: 132%;
      height: 100%;
      background-image: url('../img/bg/bg-advantages-circles5.png');
      background-repeat: no-repeat;

      @include viewport--sm {
        width: calc(100% + 54px);
        top: -42px;
        left: 67%;
        background-image: url('../img/svg/bg-advantages-circles-mobile5.svg');
      }
    }
  }

  img {
    width: 100%;
    max-height: 260px;
    object-fit: cover;
  }

  @include viewport--sm {
    margin-bottom: 52px;
    width: 100%;

    &:nth-child(1)::after {
      content: "";
      z-index: 0;
      position: absolute;
      top: -24px;
      left: -53px;
      width: 158px;
      height: 112px;
      background-image: url('../img/svg/bg-advantages-circles-mobile1.svg');
      background-repeat: no-repeat;
    }

    &:nth-child(3) .advantages__description::after {
      content: "";
      z-index: 0;
      position: absolute;
      top: -65px;
      left: 81%;
      width: 107px;
      height: 158px;
      background-image: url('../img/svg/bg-advantages-circles-mobile3.svg');
      background-repeat: no-repeat;
    }
  }
}

.advantages__description {
  position: relative;
  z-index: 1;
  padding-right: 3%;
  @include viewport--sm {
    margin: 0 24px;
    padding-right: 17px;
  }
}

.advantages__title {
  margin: 42px 0 17px;
  color: $color-black-dark;
  font-family: $font-family--second;
  font-size: 32px;
  line-height: 38px;
  font-weight: 800;
  letter-spacing: -1px;
}

.advantages__text {
  margin: 0;
  letter-spacing: -0.2px;
  position: relative;
  z-index: 1;
}

.advantages__video {
  position: relative;
  margin-bottom: 40px;

  &:focus {
    outline: none;
  }

  button {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    top: 50%;
    left: 50%;
    right: 0;
    bottom: 0;
    cursor: pointer;
    width: 128px;
    height: 128px;
    min-width: 128px;
    transform: translate(-50%, -50%);
  }

  video {
    margin: 0 auto;
    object-fit: cover;

    &:focus {
      outline: none;
    }
  }

  @include viewport--sm {
    button {
      width: 96px;
      height: 96px;
      min-width: 96px;

      svg {
        width: 70px;
        height: 70px;
      }
    }
  }
}
