@mixin retina {
  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    @content;
  }
}

// Desktop first

@mixin viewport--xl {
  @media (max-width: $viewport--xxl - 1) {
    @content;
  }
}

@mixin viewport--lg {
  @media (max-width: $viewport--xl - 1) {
    @content;
  }
}

@mixin viewport--md {
  @media (max-width: $viewport--lg - 1) {
    @content;
  }
}

@mixin viewport--sm {
  @media (max-width: $viewport--md - 1) {
    @content;
  }
}

@mixin list-style-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}

@mixin input-style-reset {
  padding: 0;

  border: none;

  &:focus {
    outline: none;
  }
}

@mixin button-style-reset {
  padding: 0;

  font-size: 0;
  color: inherit;

  background: none;
  outline: inherit;
  border: none;

  cursor: pointer;
}

@mixin price {
  font-family: $font-family--second;
  font-size: 32px;
  font-weight: 800;
  line-height: 48px;

  @include viewport--md {
    font-size: 21px;
    line-height: 32px;
  }
}

@mixin price-block {
  .price-block__item {
    @include price;
    color: $color-violet;
    margin-right: 19px;

    @include viewport--md {
      font-size: 24px;
    }

    &--old {
      font-size: 27px;
      text-decoration: line-through;
      color: $color-orange;
      letter-spacing: -0.3px;

      @include viewport--md {
        font-size: 20px;
      }
    }

    @include viewport--md {
      margin-right: 10px;
    }
  }
}

@mixin on-hover {
  @media (min-width: $viewport--lg) {
    transition: opacity $transition;

    &:not(.btn):hover {
      cursor: pointer;
      opacity: $opacity-hover;
    }
  }
}

@mixin center-xy-by-flex-row {
  display: flex;
  align-items: center;
  justify-content: center;
}

@mixin center-y-by-flex-column {
  display: flex;
  flex-direction: column;
  align-items: center;
}

@mixin h2 {
  margin: 0;
  font-family: $font-family--second;
  font-weight: 800;
  font-size: 48px;
  line-height: 62px;
  letter-spacing: -1px;
}

@mixin input-style {
  @include input-style-reset;
  width: 100%;
  padding: 23px 17px 20px;

  font-size: 18px;
  line-height: 27px;
  font-weight: 500;
  letter-spacing: -0.2px;
  color: $color-black-dark;

  border: 1px solid $color-periwinkle;
  box-shadow: $shadow-periwinkle-25;
  border-radius: 10px;

  &::placeholder {
    color: $color-black-light;
    font-weight: normal;
    letter-spacing: -0.2px;
  }

  &:focus {
    padding: 32px 17px 11px;

    box-shadow: $shadow-periwinkle-45;

    &::placeholder {
      color: transparent;
    }

    & + label {
      position: absolute;
      top: 13px;
      left: 18px;

      display: inline;

      font-size: 14px;
      line-height: 19px;
      letter-spacing: -0.2px;

      @include viewport--md {
        top: 9px;
      }
    }

    @include viewport--md {
      padding: 28px 17px 7px;
    }
  }

  @include viewport--md {
    padding: 18px 17px 17px;
  }
}
