.btn {
  @include button-style-reset;
  @include on-hover;

  display: flex;
  align-items: center;
  justify-content: center;

  padding: 5px 9px 7px;
  min-width: 352px;

  line-height: 27px;
  letter-spacing: -0.2px;
  color: $color-black-light;
  background: $color-default-white;

  border-radius: 10px;
  box-shadow: $shadow-periwinkle-25;
}

.btn svg {
  flex-shrink: 0;
  margin: 0 12px 0 0;
}

.btn--color {
  font-size: 24px;
  line-height: 48px;
  letter-spacing: -0.3px;
  font-family: $font-family--second;
  font-weight: 800;
  color: $color-default-white;
  background-image: $button-color;
  transition: background-image $transition;

  @media (min-width: $viewport--lg) {
    &:hover,
    &:focus {
      opacity: 1;
      background-image: $button-color-hover;
    }
  }

  &:active {
    background-image: $button-color-focus;
    outline: none;
    box-shadow: none;
  }

  @include viewport--md {
    font-size: 20px;
  }
}

.btn--light {
  color: $color-violet;
  background-image: $button-color-light;

  @media (min-width: $viewport--lg) {
    &:hover,
    &:focus {
      background-image: $button-color-light-hover;
    }
  }

  &:active {
    background-image: $button-color-light-focus;
  }
}

.btn--oval {
  padding: 9px 31px 12px;
  min-width: auto;

  font-size: 18px;
  line-height: 27px;
  letter-spacing: -0.3px;

  border-radius: 25px;

  @include viewport--md {
    padding: 3px 15px 5px;

    font-size: 16px;
    line-height: 24px;
  }
}

.btn--oval svg {
  margin: 0;
}

.btn--circle {
  border-radius: 50%;
}

.btn--circle svg {
  margin: 0;
}
