.header {
  padding: 19px 0;

  background-color: $color-default-white;
  box-shadow: $shadow-grey;

  &--modal {
    padding: 27px 0;

    & .container {
      @include viewport--md {
        padding: 0 22px 0 24px;
      }
    }
  }

  @include viewport--md {
    padding: 6px 0 7px;
  }
}

.header:not(.header--modal) .container {
  @include viewport--md {
    padding: 0 14px 0 24px;
  }
}

.header__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 5px 0;
}

.header__logo {
  display: flex;

  @include viewport--md {
    max-width: 143px;
  }
}

.header nav {
  @include viewport--md {
    display: none;
  }
}

.header ul {
  @include list-style-reset;

  display: flex;
  flex-wrap: wrap;
  margin: 0 25px 0 0;
}

.header li {
  display: flex;
  align-items: center;
  margin: 0 0 0 39px;

  letter-spacing: -0.2px;
}

.header a:not(.header__logo) {
  padding: 5px;

  color: $color-black-light;

  opacity: 0.3;
}

.header a[href] {
  @include on-hover;
}
