.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;

  display: flex;
  flex-direction: column;
  overflow-y: auto;

  background-color: rgba($color-default-black, 0.6);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  pointer-events: none;

  -ms-overflow-style: none;
  scrollbar-width: none;

  transition: transform $modal-transition, visibility $modal-transition, opacity $modal-transition;

  &::-webkit-scrollbar {
    display: none;
  }

  &--preload {
    display: none;
  }

  &--active {
    transform: scale(1);
    visibility: visible;
    opacity: 1;
    pointer-events: auto;
  }

  &--feedback {
    & .container {
      background-image: url("../img/bg/bg-modal-feedback-desktop@1x.png");
      background-position: right calc(50% - 83px) bottom 0;
      background-size: 620px 765px;
      background-repeat: no-repeat;

      @include retina {
        background-image: url("../img/bg/bg-modal-feedback-desktop@2x.png");
      }

      @include viewport--md {
        background-image: url("../img/bg/bg-modal-feedback-mobile@1x.png");
        background-position: right 0 top -4px;
        background-size: 154px 356px;
        background-repeat: no-repeat;

        @include retina {
          background-image: url("../img/bg/bg-modal-feedback-mobile@2x.png");
        }
      }
    }

    & .modal__inner-content {
      padding-bottom: 134px;

      @include viewport--md {
        padding-bottom: 18px;

        & h2 {
          max-width: 80%;
        }

        & h2 + p {
          max-width: 80%;
        }
      }
    }

    & button[type="submit"] svg {
      fill: $color-default-white;

      @include viewport--md {
        max-width: 24px;
      }
    }
  }

  &--order {
    & .container {
      background-image: url("../img/bg/bg-modal-order-desktop@1x.png");
      background-position: right calc(25% - 152px) bottom 0;
      background-size: 833px 710px;
      background-repeat: no-repeat;

      @include retina {
        background-image: url("../img/bg/bg-modal-order-desktop@2x.png");
      }

      @include viewport--md {
        background-image: url("../img/bg/bg-modal-order-mobile@1x.png");
        background-position: right 0 top -10px;
        background-size: 182px 392px;
        background-repeat: no-repeat;

        @include retina {
          background-image: url("../img/bg/bg-modal-order-mobile@2x.png");
        }
      }
    }

    & .modal__inner-content {
      max-width: 66%;
      padding-bottom: 78px;

      @include viewport--md {
        max-width: 100%;
        padding-bottom: 20px;

        & h2 {
          max-width: 80%;
        }
      }
    }

    & h2 + p {
      max-width: 80%;
    }

    & button svg {
      @include viewport--md {
        max-width: 24px;
      }
    }
  }

  &--success {
    & .modal__content {
      min-height: 875px;

      @include viewport--md {
        min-height: 641px;
      }
    }

    & .container {
      background-image: url("../img/bg/bg-modal-success-desktop@1x.png");
      background-position: right calc(50% - 184px) top 49px;
      background-size: 241px 283px;
      background-repeat: no-repeat;

      @include retina {
        background-image: url("../img/bg/bg-modal-success-desktop@2x.png");
      }

      @include viewport--md {
        background-image: url("../img/bg/bg-modal-success-mobile@1x.png");
        background-position: right 0 top 31px;
        background-size: 57px 182px;
        background-repeat: no-repeat;

        @include retina {
          background-image: url("../img/bg/bg-modal-success-mobile@2x.png");
        }
      }
    }

    & h2 span {
      display: block;
    }

    & .modal__inner-content {
      max-width: 537px;
      padding-bottom: 233px;

      @include viewport--md {
        max-width: 480px;
        padding-bottom: 197px;

        & h2 + p {
          max-width: 85%;
        }
      }
    }

    & .btn--color span {
      @include viewport--md {
        margin: 0 14px 0 0;
      }
    }
  }

  & header .container {
    background-image: none;
  }
}

.modal__wrapper {
  position: relative;

  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
}

.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;

  width: 100%;
  height: 100%;

  cursor: pointer;
}

.modal__content {
  position: relative;

  width: 100%;
  max-width: $max-modal-desktop-width;

  background-color: $color-default-white;
}

.modal__form {
  max-width: 352px;
}

.modal .btn {
  & svg {
    margin: 0 10px 0 0;
  }
}

.modal__form button {
  margin: 24px 0 0;
}

.modal__close-btn {
  @include center-xy-by-flex-row;
  @include on-hover;

  @include viewport--md {
    & svg {
      max-width: 16px;
      max-height: 16px;
    }
  }
}

.modal__inner-content {
  padding: 68px 0;
  max-width: 500px;

  @include viewport--md {
    padding: 29px 0;
  }
}

.modal__inner-content h2 {
  margin: 0 0 32px;

  font-family: $font-family--second;
  font-weight: 800;
  font-size: 56px;
  line-height: 72px;
  letter-spacing: -1px;
  color: $color-black-dark;

  @include viewport--md {
    margin: 0 0 22px;

    font-size: 32px;
    line-height: 38px;
  }
}

.modal__inner-content p {
  margin: 0 0 40px;

  font-size: 21px;
  line-height: 35px;
  letter-spacing: -0.2px;

  @include viewport--md {
    margin: 0 0 24px;

    font-size: 18px;
    line-height: 27px;
  }
}

.modal__inner-content .btn {
  min-height: 72px;
  min-width: 100%;

  @include viewport--md {
    min-height: auto;
  }
}

.modal__privacy {
  @media (min-width: $viewport--lg) {
    display: none;
  }
}

.modal__privacy p {
  margin: 25px 0 0;
  max-width: calc(50% + 155px);

  font-size: 14px;
  line-height: 19px;
  letter-spacing: -0.2px;
  color: $color-fossil;
}

.modal__privacy a {
  color: $color-fossil;
  text-decoration: underline;
}

.modal__order-item {
  display: flex;
  flex-wrap: wrap;
  padding: 8px 26px 16px 23px;

  background-color: $color-default-white;
  border: 1px solid $color-periwinkle;
  box-shadow: $shadow-periwinkle-25;
  border-radius: 15px;
}

.modal__order-image {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  width: 80px;
}

.modal__order-item p {
  width: 100%;
  margin: 0;

  font-family: $font-family--second;
  font-weight: 800;
  font-size: 21px;
  line-height: 31px;
  color: $color-black-dark;
}

.modal__order-description {
  display: flex;
  margin: 0 0 7px;
}

.modal__order-description picture {
  flex-shrink: 0;
}

.modal__order-description p {
  margin: 0 0 0 10px;
  padding: 8px 0;

  font-family: $font-family--main;
  font-weight: normal;
  font-size: 16px;
  line-height: 22px;
  color: $color-black-light;
  letter-spacing: normal;
}

.modal__contacts {
  padding: 4px 0 0;
  margin: 0 0 47px;

  @include viewport--md {
    padding: 0;
    margin: 0 0 33px;
  }
}

.modal__contacts p {
  margin: 0;
}

.modal__contacts a {
  @include on-hover;
  display: flex;
  align-items: center;

  font-family: $font-family--second;
  font-weight: 800;
  font-size: 27px;
  line-height: 34px;
  color: $color-black-light;

  @include viewport--md {
    padding: 0 0 0 5px;

    font-size: 21px;
    line-height: 37px;
  }
}

.modal__contacts svg {
  max-width: 20px;
  margin: 0 15px 0 0;

  fill: $color-black-light;

  @include viewport--md {
    max-width: 16px;
    margin: 0 10px 0 0;
  }
}

.modal__contacts + .btn {
  min-width: 352px;

  @include viewport--md {
    min-width: 100%;
  }
}

.modal__contacts + .btn svg {
  max-width: none;

  @include viewport--md {
    max-width: 16px;
  }
}
