.features {
  position: relative;
	padding: 43px 5.6% 0;

	background-image: url('../img/bg/bg-features.jpg');
	background-repeat: no-repeat;
	background-size: cover;
  background-position: center center;
  overflow: hidden;

	h2 {
		@include h2;
    margin: 0 0 51px;
    width: 100%;
		color: $color-default-white;

		@include viewport--md {
			margin-bottom: 41px;
			font-size: 32px;
			line-height: 38px;
		}
	}

	&::before {
    content: '';
    position: absolute;
    bottom: -175px;
    left: 26.7%;
    width: 428px;
    height: 355px;
    z-index: 0;

    background-image: url('../img/bg/bg-features-circles.png');
	  background-repeat: no-repeat;

    @include viewport--md {
      background-position: bottom -30px right -70px;
      background-size: 247px;
    }
  }

  &::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 0;

    background-image: url('../img/bg/cleaner.png');
	  background-repeat: no-repeat;
    background-position: bottom -30px right -90px;

    @include viewport--md {
      background-position: bottom -30px right -70px;
      background-size: 247px;
    }
  }

  @include viewport--md {
		margin: 0 -24px;
		padding: 33px 24px 0;
	}
}

.features__wrapper {
	display: flex;
  justify-content: space-between;

  @include viewport--md {
		display: block;
	}
}

.features__slider-list {
  position: relative;
  z-index: 1;
  display: inline-block;
  width: 100%;
}

.features__slider {
  @include viewport--md {
    margin-right: -24px;
    margin-left: -24px;
	}
}

.features__list {
	@include list-style-reset;

	display: flex;
  flex-wrap: wrap;
	margin: 0 -36px;
	padding-bottom: 10px;
  width: calc(100% + 36px);
  max-width: 925px;

	@include viewport--md {
		margin: 0;
    padding-bottom: 14px;
    padding-left: 24px;
		width: calc(100% + 24px);
		flex-wrap: nowrap;
  }

  @include viewport--sm {
    width: 100%;
	}
}

.features__item {
	margin: 0 36px 27px;
	width: 156px;

	p {
		font-size: 16px;
		line-height: 22px;
		color: $color-default-white;
	}

	@include viewport--md {
		margin: 0 56px 0 0;
	}
}

.features__pagination {
	text-align: start;

	.swiper-pagination-bullet {
    margin: 0 12px 0 0;
    opacity: 0.3;
    background: $color-default-white;
  }

  .swiper-pagination-bullet-active {
    opacity: 1;
  }
}

.features__contact {
  position: relative;
  z-index: 1;
	display: flex;
	flex-direction: column;
  justify-content: flex-end;
	width: 422px;

	button {
		margin-bottom: 63px;
		padding-top: 12px;
		padding-bottom: 12px;
		min-width: 336px;
		width: 336px;

		font-family: $font-family--second;
		font-weight: 800;
		font-size: 24px;
		line-height: 48px;
		letter-spacing: -0.3px;
		color: $color-violet;

		svg {
			margin-right: 8px;
      vertical-align: text-top;
			fill: $color-violet;

			@include viewport--md {
				width: 24px;
				height: 24px;
			}
		}

		@include viewport--md {
      margin-bottom: 19px;
      padding-top: 6px;
		  padding-bottom: 6px;
      font-size: 20px;
      min-width: 311px;
		  width: 311px;
    }

    @include viewport--sm {
      min-width: 100%;
		  width: 100%;
		}
	}

	@include viewport--md {
		margin: -70px -24px 0 0;
		padding-top: 140px;
		width: 100%;
		background-size: 300px;
		background-position: bottom 39px right -71px;
	}
}

.features__message {
	position: relative;
	display: block;
	margin: 0 0 7px;
	padding: 14px 21px;
	width: 204px;

	color: $color-default-white;
	line-height: 21px;
	letter-spacing: -0.2px;
	border-radius: 15px;
	background-color: $color-blue-lighten;

	&--full {
		margin-bottom: 46px;
		width: 266px;

		&::after {
			content: '';
			right: -1px;
      bottom: -5px;
			display: inline-block;
			position: absolute;
			width: 22px;
			height: 31px;
			background-image: url('../img/svg/icon-tail.svg');
    }

    @include viewport--md {
      margin-bottom: 32px;
    }
	}
}
