.components {
  .products-block {
    background-color: $color-white-dark;
  }

  table {
    td {
      vertical-align: baseline;
      padding: 14px 0;
      width: 57%;
      font-size: 16px;
      line-height: 22px;
      border-top: $border-grey;

      &:nth-child(2n) {
        width: 43%;
        text-align: right;

        @include viewport--md {
          width: 60%;
        }
      }

      @include viewport--md {
        width: 30%;
      }
    }

    @include viewport--md {
      width: 100%;
    }
  }

  .products-block__text {
    padding-top: 31px;
  }

  .products-block__photo {
    margin: 0;
    height: 161px;
  }

  @include viewport--md {
    padding: 38px 0 0;
    flex-wrap: wrap;
    justify-content: center;

    .products-block__photo {
      margin: 0 auto;
      padding: 0 28px;
    }
  }
}

.components__title {
  @include h2;
  padding: 42px 0;
  font-size: 32px;
  line-height: 48px;
  letter-spacing: 0;
  color: $color-black-dark;
  border-top: $border-grey;

  @include viewport--md {
    padding-top: 32px;
    padding-bottom: 38px;
    font-size: 21px;
    line-height: 32px;
  }
}
