.components table td {
  vertical-align: baseline;
  padding: 14px 0;
  width: 57%;
  font-size: 16px;
  line-height: 22px;
  border-top: 1px solid rgba(166, 171, 189, 0.2); }
  .components table td:nth-child(2n) {
    width: 43%;
    text-align: right; }
    @media (max-width: 1023px) {
      .components table td:nth-child(2n) {
        width: 60%; } }
  @media (max-width: 1023px) {
    .components table td {
      width: 30%; } }

@media (max-width: 1023px) {
  .components table {
    width: 100%; } }

.components .products-block__text {
  padding-top: 31px; }

.components .products-block__photo {
  margin: 0;
  height: 161px; }

@media (max-width: 1023px) {
  .components {
    padding: 38px 0 0;
    flex-wrap: wrap;
    justify-content: center; }
    .components .products-block__photo {
      margin: 0 auto;
      padding: 0 28px; } }

.components__title {
  margin: 0;
  font-family: "MPLUSRounded1c", "Arial", sans-serif;
  font-weight: 800;
  font-size: 48px;
  line-height: 62px;
  letter-spacing: -1px;
  padding: 42px 0;
  font-size: 32px;
  line-height: 48px;
  letter-spacing: 0;
  color: #19191b;
  border-top: 1px solid rgba(166, 171, 189, 0.2); }
  @media (max-width: 1023px) {
    .components__title {
      padding-top: 32px;
      padding-bottom: 38px;
      font-size: 21px;
      line-height: 32px; } }

