.page-info {
  @include price-block;

  h1 {
    display: none;
  }

  .container {
    padding-right: 0;
  }
}

.page-info__title {
  margin: 0;
  font-weight: 800;
  font-family: $font-family--second;
  font-size: 56px;
  line-height: 72px;
  letter-spacing: -1px;
  color: $color-black-dark;

  span {
    display: block;
    margin-bottom: 32px;
    font-weight: 400;

    @include viewport--md {
      margin-right: -20%;
      margin-bottom: 19px;
    }
  }

  @include viewport--md {
    padding-right: 20%;
    font-size: 32px;
    line-height: 38px;
  }
}

.page-info__wrapper {
  display: flex;
  justify-content: space-between;
  padding: 112px 0 62px;

  button {
    display: block;
    min-width: 336px;

    padding-top: 10px;
    padding-bottom: 14px;

    svg {
      margin-right: 12px;
      vertical-align: text-top;

      @include viewport--md {
        width: 24px;
        height: 24px;
      }
    }

    @include viewport--md {
      padding-top: 5px;
      padding-bottom: 7px;
      min-width: 100%;
    }
  }

  @include viewport--md {
    flex-direction: column-reverse;
    margin-left: -24px;
    padding: 0 0 66px;
  }
}

.page-info__text {
  position: relative;
  z-index: 3;
  max-width: 562px;

  @include viewport--md {
    margin-top: -40px;
    padding: 0 24px;
    width: 100%;
  }
}

.page-info__picture {
  position: relative;
  min-width: 673px;
  margin-top: -112px;

  background-image: url("../img/bg/bg-page-main.png");
  background-repeat: no-repeat;
  background-position: top -96px right 0;

  img {
    margin-top: -95px;
  }

  &::after {
    content: "";
    position: absolute;
    top: 81px;
    right: -63px;
    width: 627px;
    height: 486px;
    background-image: url('../img/bg/bg-page-main-circles.png');
    background-repeat: no-repeat;

    @include viewport--md {
      top: 24px;
      right: -19px;
      width: 443px;
      height: 272px;
      background-image: url('../img/bg/bg-page-main-circles-mobile.png');
    }
  }

  @include viewport--md {
    margin: 0;
    min-width: 320px;

    background-image: url("../img/bg/bg-page-main-mobile.png");
    background-position: right 0 bottom 67px;
    background-size: cover;

    img {
      margin-top: -79px;
      margin-left: auto;
      margin-right: 7%;
      width: 300px;
    }
  }

  @media (max-width: 500px) {
    img {
      width: 55.8%;
    }
  }

  @include viewport--sm {
    background-size: cover;
  }
}

.page-info__subtitle {
  margin: 0 0 52px;

  color: $color-black-light;
  font-size: 21px;
  line-height: 35px;
  letter-spacing: -0.2px;

  @include viewport--md {
    margin-bottom: 26px;
    font-size: 18px;
    line-height: 27px;
  }
}

.page-info__price {
  margin-bottom: 25px;

  @include viewport--md {
    margin-bottom: 32px;
    font-size: 24px;
    line-height: 36px;
  }
}
